body{
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #fff1cc;
}
h1,h2,h3,h4,h5,p,body{
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  
}

.dash-container{
  padding: 10px;
  width: 95%;
  margin: 10px auto;
  border-radius: 3px;
  box-shadow: 2px 2px 10px rgba($color: #000000, $alpha: 0.1);
  background-color: #fff;
  display: flex;
  .dash-header{
    display: flex;
    flex: 1;
    flex-direction: column;
    input.title{
      padding: 3px 5px;
      border: 1px solid coral;
      border-radius: 3px;
      background-color: #fffaec;
    }
    textarea.content{
      height: 100px;
      resize: vertical;
      margin-top: 5px;
      padding: 3px 5px;
      border: 1px solid coral;
      border-radius: 3px;
      background-color: #fffaec;
    }
    .isupdate{
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
    }
    .btn-save{
      margin: 5px 0;
      padding: 3px 5px;
      border: 1px solid coral;
      background-color: coral;
      color: white;
      border-radius: 3px;
      transition: .3s;
      &:hover{
        background-color: #fff;
        color: coral;
      }
    }
    .btn-cancel{
      margin: 5px;
      padding: 3px 5px;
      border: 1px solid coral;
      background-color: lightcoral;
      color: white;
      border-radius: 3px;
      transition: .3s;
      &:hover{
        background-color: #fff;
        color: lightcoral;
      }
    }
  }
  .dash-notes{
    margin-left: 5px;
    flex: 2;
    display: flex;
    max-height: 92vh;
    overflow: auto;
    flex-direction: column;
    padding: 10px 10px;
    background-color: #fffaec;
    border-radius: 3px;
    box-shadow: inset 2px 2px 5px rgba($color: #000000, $alpha: 0.2);
    box-sizing: border-box;
    .card-container{
      position: relative;
      .delete-confirm{
        position: absolute;
        right: 0;
        top: 50%;
        margin-right: 10px;
        transform: translateY(-50%);
        display: flex;
        flex-direction: column;
        z-index: 1;
        transition: .3s;
        button{
          border: 2px solid red;
          border-radius: 3px;
          background-color: white;
          margin: 2px 0;
          color: red;
          transition: .2s;
          &:hover{
            transform: translateX(2px);
            box-shadow: 2px 0px 5px rgba($color: #000000, $alpha: .2);
          }
        }
        .delete-confirm-no{
          border: 2px solid orange;
          color: orange;
        }
      }
      .dash-card{
        position: relative;
        margin: 5px 0;
        background-color: coral;
        padding: 8px;
        display: flex;
        flex-direction: column;
        border-radius: 3px;
        color: white;
        box-shadow: 1px 1px 5px rgba($color: #000000, $alpha: 0.2);
        box-sizing: border-box;
        transition: .3s;
        z-index: 2;
        &.dash-card.delete-slide{
          transform: translate(-80px, -2px);
          opacity: 0.8; 
        }
        &:hover{
          background-color: #ff8e64;
          transform: translate(0, -2px);
          box-shadow: 1px 3px 5px rgba($color: #000000, $alpha: 0.2);
          cursor: pointer;
        }
        .dash-title{
          display: flex;
          h3{
            margin-right: 10px;
          }
          p{
            background-color: #fff;
            padding: 1px 5px;
            margin: 3px 0;
            border-radius: 3px;
            color: coral;
          }
        }
        .btn-delete{
          background-color: maroon;
          width:60px;
          border-radius: 3px;
          text-align: center;
          align-self: flex-end;
          padding: 2px;
          &:hover{
            background-color: #ae0606;
          }
        }
      }
    }
    
  }

}
